<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0" class="demo-ruleForm">
    <div class="header">水表</div>
    <el-form-item prop="cost_w_s_number">
      <div class="diy-input">
        <div class="label">上次读数</div>
        <div class="value">{{ ruleForm.cost_w_s_number }}</div>
      </div>
    </el-form-item>
    <el-form-item prop="cost_w_e_number">
      <el-input type="number" v-model="ruleForm.cost_w_e_number" autocomplete="off"
        @blur="validateFun('cost_w_s_number', 'cost_w_e_number')">
        <template slot="prepend">本次读数</template>
      </el-input>
    </el-form-item>
    <div class="header">电表</div>
    <el-form-item prop="cost_e_s_number">
      <div class="diy-input">
        <div class="label">上次读数</div>
        <div class="value">{{ ruleForm.cost_e_s_number }}</div>
      </div>
    </el-form-item>
    <el-form-item prop="cost_e_e_number">
      <el-input type="number" v-model="ruleForm.cost_e_e_number" autocomplete="off"
        @blur="validateFun('cost_e_s_number', 'cost_e_e_number')">
        <template slot="prepend">本次读数</template>
      </el-input>
    </el-form-item>
    <div class="header">气表</div>
    <el-form-item prop="cost_g_s_number">
      <div class="diy-input">
        <div class="label">上次读数</div>
        <div class="value">{{ ruleForm.cost_g_s_number }}</div>
      </div>
    </el-form-item>
    <el-form-item prop="cost_g_e_number">
      <el-input type="number" v-model="ruleForm.cost_g_e_number" autocomplete="off"
        @blur="validateFun('cost_g_s_number', 'cost_g_e_number')">
        <template slot="prepend">本次读数</template>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button class="submit" type="primary" @click="submitForm('ruleForm')">计算费用</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import api from '@/api/api.js'
import { mapState } from 'vuex'
export default {
  name: 'Calculate',
  data() {
    return {
      ruleForm: {
      },
      rules: {
      }
    }
  },
  computed: {
    ...mapState(['userInfo']),
  },
  methods: {
    validateFun(name_pre, name_now) {
      if (this.ruleForm[name_now] < this.ruleForm[name_pre]) {
        this.$message('本次读数需大于等于上次读数')
        this.$set(this.ruleForm, name_now, '')
      }
    },
    getInit() {
      api.initCost({ user_id: this.userInfo.user_id }).then(res => {
        if (res.code === 1) {
          let data = { ...res.data }
          for (let key in data) {
            if (data[key] != 0) {
              this.$set(this.ruleForm, key, data[key])
            }
          }
        } else {
          this.$message(res.message)
        }
      })
    },
    submitForm(formName) {
      if (this.ruleForm.cost_w_e_number && this.ruleForm.cost_e_e_number && this.ruleForm.cost_g_e_number) {
        api.calculateCost({ ...this.ruleForm, cost_contract_no: this.userInfo.user_contract_no }).then(res => {
          if (res.code === 1) {
            this.$store.commit('setCalculate', res.data)
            this.$router.push('/Cost')
          } else {
            this.$message(res.message)
          }
        })
      } else {
        this.$message('请输入本次读数')
      }
    }
  },
  mounted() {
    this.getInit()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.demo-ruleForm {
  width: 80%;
  margin: 50px auto 0;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.label {
  text-align: left;
}

.header {
  text-align: left;
  font-weight: bold;
  font-size: 14px;
  margin: 15px 0;
}

.el-form-item {
  margin-bottom: 10px;
}

.submit {
  width: 100%;
  margin-top: 15px;
}

.diy-input {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.diy-input .label {
  background-color: #F5F7FA;
  color: #909399;
  border: 1px solid #DCDFE6;
  border-radius: 4px;
  padding: 0 20px;
  white-space: nowrap;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.diy-input .value {
  background-color: #FFF;
  border-radius: 4px;
  border: 1px solid #DCDFE6;
  color: #606266;
  line-height: 40px;
  padding: 0 15px;
  -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  width: 100%;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 0;
  text-align: left;
  color: #888;
  background-color: #F5F7FA;
}
</style>



