<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0" class="demo-ruleForm">
    <div class="header">水</div>
    <el-form-item prop="cost_w_number">
      <div class="diy-input">
        <div class="label">读数</div>
        <div class="value">{{ ruleForm.cost_w_number }}</div>
      </div>
    </el-form-item>
    <el-form-item prop="cost_w_money">
      <div class="diy-input">
        <div class="label">水费</div>
        <div class="value">{{ ruleForm.cost_w_money }}</div>
      </div>
    </el-form-item>
    <div class="header">电</div>
    <el-form-item prop="cost_e_number">
      <div class="diy-input">
        <div class="label">读数</div>
        <div class="value">{{ ruleForm.cost_e_number }}</div>
      </div>
    </el-form-item>
    <el-form-item prop="cost_e_money">
      <div class="diy-input">
        <div class="label">电费</div>
        <div class="value">{{ ruleForm.cost_e_money }}</div>
      </div>
    </el-form-item>
    <div class="header">气</div>
    <el-form-item prop="cost_g_number">
      <div class="diy-input">
        <div class="label">读数</div>
        <div class="value">{{ ruleForm.cost_g_number }}</div>
      </div>
    </el-form-item>
    <el-form-item prop="cost_g_money">
      <div class="diy-input">
        <div class="label">气费</div>
        <div class="value">{{ ruleForm.cost_g_money }}</div>
      </div>
    </el-form-item>
    <div class="header total">合计：{{ ruleForm.cost_total_money }}元<span class="tips">（请提交后，联系管理员缴费）</span></div>
    <el-form-item>
      <el-button class="submit" type="primary" @click="submitForm('ruleForm')">提交</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import api from '@/api/api.js'
import { mapState } from 'vuex'
export default {
  name: 'Calculate',
  data() {
    return {
      timer:null,
      rules: {
      }
    }
  },
  methods: {
    moneyFormat(num, decimal = 2, split = ',') {
      /*
        parameter：
        num：格式化目标数字
        decimal：保留几位小数，默认2位
        split：千分位分隔符，默认为,
        moneyFormat(123456789.87654321, 2, ',') // 123,456,789.88
      */
      if (isFinite(num)) { // num是数字
        if (num === 0) { // 为0
          return num.toFixed(decimal)
        } else { // 非0
          var res = ''
          var dotIndex = String(num).indexOf('.')
          if (dotIndex === -1) { // 整数
            res = String(num).replace(/(\d)(?=(?:\d{3})+$)/g, `$1${split}`) + '.' + '0'.repeat(decimal)
          } else { // 非整数
            // js四舍五入 Math.round()：正数时4舍5入，负数时5舍6入
            // Math.round(1.5) = 2
            // Math.round(-1.5) = -1
            // Math.round(-1.6) = -2
            // 保留decimals位小数
            const numStr = String((Math.round(num * Math.pow(10, decimal)) / Math.pow(10, decimal)).toFixed(decimal)) // 四舍五入，然后固定保留2位小数
            const decimals = numStr.slice(dotIndex, dotIndex + decimal + 1) // 截取小数位
            res = String(numStr.slice(0, dotIndex)).replace(/(\d)(?=(?:\d{3})+$)/g, `$1${split}`) + decimals
          }
          return res
        }
      } else {
        return '--'
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$confirm('提交后将无法修改, 是否提交?', '提示', {
            confirmButtonText: '继续提交',
            cancelButtonText: '返回重填',
            type: 'warning'
          }).then(() => {
            api.saveCost({ ...this.ruleForm }).then(res => {
              if(res.code == 1){
                this.$message(res.message)
                this.timer = null
                timer = setTimeout(()=>{
                  this.$router.push('/')
                },1000)
              }else{
                this.$message(res.message)
              }
            })
          }).catch(() => {
            this.$router.push('/Calculate')
          });
        } else {
          return false
        }
      })
    }
  },
  computed: {
    ...mapState(['calculateData']),
    ruleForm() {
      let data = { ...this.calculateData }
      for (let key in data) {
        if (key.indexOf('_money') != -1) {
          data[key] = this.moneyFormat(data[key])
        }
      }
      return data
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.demo-ruleForm {
  width: 80%;
  margin: 50px auto 0;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.header {
  text-align: left;
  font-weight: bold;
  font-size: 14px;
  margin: 15px 0;
}

.total {
  color: #f00;
  margin-top: 20px;
}

.tips {
  font-size: 12px;
  font-weight: normal;
  color: #888;
}

.el-form-item {
  margin-bottom: 10px;
}

.submit {
  width: 100%;
  margin-top: 10px;
}
.diy-input {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.diy-input .label {
  background-color: #F5F7FA;
  color: #909399;
  border: 1px solid #DCDFE6;
  border-radius: 4px;
  padding: 0 20px;
  white-space: nowrap;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.diy-input .value {
  background-color: #FFF;
  border-radius: 4px;
  border: 1px solid #DCDFE6;
  color: #606266;
  line-height: 40px;
  padding: 0 15px;
  -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  width: 100%;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 0;
  text-align: left;
  color: #888;
  background-color: #F5F7FA;
}
</style>



