import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    userInfo:{
        user_contract_no:'',
        user_id:'',
        user_name:'',
        user_no:'',
        user_phone:''
    },
    calculateData:{}
  },
  mutations: {
    setUserInfo(state,data) {
      state.userInfo = data;
    },
    setCalculate(state,data) {
        state.calculateData = data;
    },
  },
});

export default store;


