import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/view/Login'
import Calculate from '@/view/Calculate'
import Cost from '@/view/Cost'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Calculate',
      name: 'Calculate',
      component: Calculate
    },
    {
      path: '/Cost',
      name: 'Cost',
      component: Cost
    }
  ]
})



