import request from '@/utils/request'

// 登录
function login (data) {
  return request({
    url: '/cattle/house/user/login',
    method: 'post',
    data
  })
}
// 初始化期初
function initCost (data) {
  return request({
    url: '/cattle/house/cost/initCost',
    method: 'post',
    data
  })
}
// 计算费用
function calculateCost (data) {
  return request({
    url: '/cattle/house/cost/calculateCost',
    method: 'post',
    data
  })
}
// 提交费用
function saveCost (data) {
  return request({
    url: '/cattle/house/cost/saveCost',
    method: 'post',
    data
  })
}
export default{
  login,
  initCost,
  calculateCost,
  saveCost
}



