<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0" class="demo-ruleForm">
    <el-form-item prop="user_no">
      <el-input prefix-icon="el-icon-user" type="text" v-model="ruleForm.user_no" autocomplete="off" placeholder="请输入用户编号/手机号"></el-input>
    </el-form-item>
    <el-form-item prop="user_password">
      <el-input prefix-icon="el-icon-lock" type="password" v-model="ruleForm.user_password" autocomplete="off" placeholder="请输入密码"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button class="submit" type="primary" plain @click="submitForm('ruleForm')">登 录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import api from '@/api/api.js'
import { mapState } from 'vuex';
var validatePass = (rule, value, callback) => {
  if (value === '') {
    callback(new Error('请输入密码'))
  } else {
    callback()
  }
}
var validateNo = (rule, value, callback) => {
  if (value === '') {
    callback(new Error('请输入用户编号/手机号'))
  } else {
    callback()
  }
}
export default {
  name: 'Login',
  data () {
    return {
      ruleForm: {
        user_no: '',
        user_password: ''
      },
      rules: {
        user_no: [
          { validator: validateNo, trigger: 'blur' }
        ],
        user_password: [
          { validator: validatePass, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState(['userInfo']),
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          api.login({...this.ruleForm, user_phone: this.ruleForm.user_no}).then(res => {
            if (res.code === 1) {
              this.$store.commit('setUserInfo',res.data)
              this.$router.push('/Calculate')
            } else {
              this.$message(res.message)
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.demo-ruleForm{
  width: 80%;
  margin: 100px auto 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.submit{
  width:100%;
}
</style>



